.container {
  width: 90%;
  max-width: 34rem;
  margin: 0 auto;
}

h1,
h3 {
  text-align: center;
  color: #fff;
}

h1 {
  margin-left: 0.4rem;
  font-size: 2.5rem;
  margin-top: 1rem;
  margin-bottom: 0.2rem;
  font-family: 'Architects Daughter', cursive;
}

h3 {
  margin-top: 0;
  font-weight: 300;
  font-size: 1.3rem;
  font-family: 'Architects Daughter', cursive;
}
